import React, { PureComponent } from "react";
import {Line} from 'react-chartjs-2';


export default class GraphView extends PureComponent<{data: any, average: number | string}> {
  render() {
    return (
      <React.Fragment>
        <h2>Your predicted grade is a <b>{ this.props.average }</b>.</h2>
        <Line
          data={{
            labels: [1, 2, 3, 4],
            datasets: [
              {
                label: "Min",
                backgroundColor: 'rgba(55, 173, 221,  0.6)',
                borderColor: 'rgba(55, 173, 221, 1.0)',
                fill: false,  //no fill here
                data: [5, 5, 3, 2]
              },
              {
                label: "Max",
                backgroundColor: 'rgba(55, 173, 221, 0.6)',
                borderColor: 'rgba(55, 173, 221, 1.0)',
                fill: '-1', //fill until previous dataset
                data: [8, 7, 6, 5]
              },
              {
                label: "Predicted grade",
                backgroundColor: 'rgba(55, 173, 221, 0.6)',
                borderColor: 'rgba(55, 173, 221, 1.0)',
                fill: false,
                data: [7, 6, 4, 4]
              }
            ],
          }}
          options={{
            legend: {
              display: false
            },
            maintainAspectRatio: false,
            spanGaps: false,
            elements: {
              line: {
                tension: 0.000001
              }
            },
            plugins: {
              filler: {
                propagate: false
              }
            },
            scales: {
              xAxes: [{
                ticks: {
                  autoSkip: false
                }
              }]
            }
          }}
        />
      </React.Fragment>
    )
  }
}